.about {
    background-color: $bgc-about-gallery-contacts-footer;
    position: relative;
}

.top-text {
    text-align: center;
    margin-bottom: 15px;


}

.about-title {
    text-align: center;
    margin-bottom: 32px;

    @include tab {
        margin-bottom: 40px;
        }
    @include desk {
        margin-bottom: 80px;
        }
}

.about-box {
    @include tab {
    display: flex;
    justify-content: space-between;
    }

    @include desk {
    justify-content: flex-start;
    align-items: center;
    }
}

.box-cow {
    margin-bottom: 40px;
    @include tab {
        max-width: 336px;
        margin-bottom: 0;
    }

    @include desk {
        min-width: 501px;
        margin-right: 139px;
    }
}

.text-first {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.6;
    max-width: 220px;
    margin-bottom: 40px;

    color: $text-color-black;

    @include tab {
        max-width: 334px;
        font-size: 14px;
    }

    @include desk {
        min-width: 463px;
        font-size: 16px;
        line-height: 1.75;
    }
}

.text-second {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.6;
    max-width: 220px;
    margin-bottom: 20px;

    color: $text-color-second;

    @include tab {
        max-width: 334px;
        font-size: 14px;
        margin-bottom: 40px;
    }

    @include desk {
        min-width: 463px;
        font-size: 16px;
        line-height: 1.75;
    }
}

.about-pic-milk {
    position: absolute;
    width: 100vw;
    left: 0;
    right: 0;
    bottom: 200px;
    @media screen and (min-width: 768px) and (max-width: 1199px){
        display: none;
    }
    @include tab {
    bottom: 0;
    }
    @include desk {
    bottom: 0;
    }
}

.about-btn {
    display: inline-flex;

    font-family: 'DM Sans';
    background-color:$color-white;
    color:$text-title-color;
    border: transparent;

    border-radius: 22px;
    min-width: 176px;
    min-height: 44px;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;

    align-items: center;
    justify-content: center;

    &::after{
        content: "";
        margin-left: 8px;
        width: 8px;
        height: 10px;
        background-image: url(../images/arrow-icon-red.png);
        background-repeat: no-repeat;
    }

    @include transition(background-color );

    &:hover,
    &:focus {
    background: $color-accent;
    color: $color-white;
        &::after{
            content: "";
            margin-left: 8px;
            width: 8px;
            height: 10px;
            background-image: url(../images/arrow-icon.png);
            background-repeat: no-repeat;
        }
    }
}

